.app__muestra {
  width: 100%;
  background: #000;
  &-header {
    // width: 100%;
    // height: 50vh;
    padding: 5vw;
    background: #FF2E00;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    &_textos {
      max-width: 50%;
      #title-convocatorias {
        color: rgba(0, 0, 0, 0.85);
        font-weight: 800;
        font-size: 42px;
        line-height: 57px;
        text-transform: uppercase;
        text-shadow: -2px -2px 0px #71C3FF, 2px 2px 0px #FFFFFF;
      }

      span {
        color: #fff;
        font-size: 42px;
      }

      p {
        color: #fff;
        font-size: 20px;
        line-height: 40px;
        font-family: 'Raleway', sans-serif;
      }
    }

    &_lista {
      ul li {
        display: flex;
        list-style: none;
        align-items: center;
        color: #fff;
        // line-height: 40px;
        font-family: 'Raleway', sans-serif;
        font-size: 20px;
        padding: 5px 0;

        img {
          background: #fff;
          border-radius: 50%;
          margin: 0 15px 0 0;
          vertical-align: middle;
        }
      }
    }
  }

  &-contenido {
    position: relative;
    width: 50%;
    text-align: left;
    margin-top: 80px;
    margin-bottom: 80px;
    margin-left: 100px;

    & .contenido__title {
      font-size: 35px;
      font-weight: 800;
      color: rgba(255, 255, 255, 0.85);
    }
    
    & .contenido__subtitle {
      font-size: 25px;
      color: #fff;
    }

    & .contenido__text {
      margin: 33px 0 0 0;
      color: #fff;
      font-size: 20px;
      line-height: 27px;
    }
  }
}

.muestra_button {
  width: 200px;
  height: 43px;
  margin: 30px 0 0 0;
  background: rgba(255, 46, 0, 0.85);
  border-radius: 10px;
  text-transform: uppercase;
  border: none;
  a {
    color: #fff;
    font-weight: 800;
    text-decoration: none;
    line-height: 23.15px;
    text-transform: uppercase;
    font-size: 16px;
  }
}

.spacer_muestra {
  margin: 10% 0;
}

@media screen and (min-width: 425px) and (max-width: 768px) {
  .app__muestra {
    &-header {
      flex-direction: column;
      &_textos {
        max-width: 100%;
        padding-bottom: 20px;
        #title-convocatorias {
          font-size: 30px;
        }

        span {
          font-size: 26px;
        }
      }

      &_lista ul li{

          font-size: 15px;
      }
    }
    
    &-contenido {
      width: 80%;
      margin: 5vw auto;
    }
  }
}

@media screen and (min-width: 320px) and (max-width: 425px) { 
  .app__muestra {
    &-header {
      // height: 80vh;
      flex-direction: column;
      &_textos {
        max-width: 100%;
        padding-bottom: 20px;
        #title-convocatorias {
          font-size: 25px;
        }

        span {
          font-size: 25px;
        }
      }

      &_lista ul li{
        font-size: 12px;
      }
    }

    &-contenido {
      width: 80%;
      margin: 5vw auto;
    }
  }
}

.spacer_muestra img{
  width: 100%;
}