.casos__timeline {
  //
  $lineColor: #ffffff;
  $avatar-size: min(max(3vw, 50px), 70px);
  $grid-row-gap: 60px;
  $text-color: #ffffff;
  //

  background-color: #000;
  color: $text-color;
  padding: 5% 5% 10% 5%;
  z-index: -2;

  &-grid {
    display: grid;
    width: 90%;
    grid-template-columns: auto 1fr;
    grid-auto-rows: auto;
    row-gap: $grid-row-gap;
    column-gap: 1vw;
    max-width: 1000px;
    margin: 0 auto;
  }

  &-column {
    position: relative;
    z-index: 1;

    &::after {
      content: "";
      position: absolute;
      margin: 0 auto;
      background-color: $lineColor;
      width: 1px;
      left: 0;
      right: 0;
      top: 0;
      bottom: calc($grid-row-gap * -1);
      z-index: -1;
    }

    &:nth-last-child(2)::after {
      display: none;
    }
  }

  &-quote {
    color: $text-color;
    font-size: 20px;
    line-height: 23.5px;
    
    p {
      padding-top: 15px;

      &:first-child {
        margin-left: 1vw;
        border-bottom: 1px solid $lineColor;
        padding-bottom: 15px;
        padding-top:0;
      }
    }
  }

  img {
    width: $avatar-size;
    height: $avatar-size;
    border-radius: 100%;
  }
}

